import Endpoint from '@/services/Endpoint'

export default {
  beneficiaryTypes (payload) {
    return Endpoint().get('/s1/beneficiary/beneficiaryType', {
      params: payload
    })
  },
  createBeneficiaryType (payload) {
    return Endpoint().post('/s1/beneficiary/beneficiaryType', payload)
  },
  modifyBeneficiaryType (payload) {
    return Endpoint().patch('/s1/beneficiary/beneficiaryType', payload)
  },
  beneficiaryTypeTree (payload) {
    return Endpoint().get('/s1/beneficiary/beneficiaryType/tree', {
      params: payload
    })
  }
}
