<template>
  <nv-card>
    <div class="flex flex-wrap-reverse items-center mb-base">
      <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
      <vs-button class="p-3 mr-4" @click="modify" color="primary" type="filled" icon="fa-pen" icon-pack="far" v-if="$acl.check('isBeneficiaryAll')">{{ $t('MODIFY') }}</vs-button>
    </div>

    <div class="row hover">
      <p class="info-row">
        <span>{{ $t('ID') }}:</span>
        <span>{{ beneficiaryType.typeId }}</span>
      </p>
      <p class="info-row">
        <span>{{ $t('TYPE_NAME') }}:</span>
        <span>
          <nv-localization-popup :active="popupActive" v-on:close="popupActive = false" path="/s1/beneficiary/beneficiaryType/localization" popupRef="localizationModal" :pkValue="this.$route.params.id" fieldName="typeName"/>
          <vs-button class="m-0 p-0" color="primary" type="flat" @click="popupActive = true"><i class="far fa-globe-africa mr-1 text-primary"></i></vs-button>
          <span :class="disabledClass(beneficiaryType)">{{ beneficiaryType.typeName }}</span>
        </span>
      </p>
      <p class="info-row" v-if="beneficiaryType.parentTypeId">
        <span>{{ $t('PARENT_TYPE') }}:</span>
        <span><router-link :to="{ name: 'ViewBeneficiaryType', params: { id: beneficiaryType.parentTypeId } }" :title="beneficiaryType.parentTypeName">{{ beneficiaryType.parentTypeName }}</router-link></span>
      </p>
      <p class="info-row">
        <span>{{ $t('TYPE_BEHAVIOUR') }}:</span>
        <span>{{ beneficiaryType.typeBehaviourDescription }}</span>
      </p>
      <p class="info-row">
        <span>{{ $t('EXTERNAL_ID') }}:</span>
        <span>{{ beneficiaryType.externalTypeId | placeholder('-') }}</span>
      </p>
      <p class="info-row">
        <span>{{ $t('POSITION') }}:</span>
        <span>{{ beneficiaryType.position | placeholder('-') }}</span>
      </p>
      <p class="info-row">
        <span>{{ $t('CREATION_DATE') }}:</span>
        <span>{{ beneficiaryType.creationDate | formatUnixTimestamp }}</span>
      </p>
    </div>

    <vs-collapse type="border" class="mt-base" v-if="beneficiaryType.inputNotes || beneficiaryType.reviewNotes">
      <vs-collapse-item v-if="beneficiaryType.inputNotes">
        <div slot="header">
          {{ $t('INPUT_NOTES') }}
        </div>
        <div v-html="beneficiaryType.inputNotes"></div>
      </vs-collapse-item>
      <vs-collapse-item v-if="beneficiaryType.reviewNotes">
        <div slot="header">
          {{ $t('REVIEW_NOTES') }}
        </div>
        <div v-html="beneficiaryType.reviewNotes"></div>
      </vs-collapse-item>
    </vs-collapse>
  </nv-card>
</template>

<script>
import BeneficiaryTypeService from '@/services/beneficiary/BeneficiaryTypeService'
import NvLocalizationPopup from '@/components/nv-localization/NvLocalizationPopup'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'

export default {
  name: 'beneficiary-type-view',
  mixins: [DisabledClassMixin],
  components: {
    NvLocalizationPopup
  },
  data: function () {
    return {
      beneficiaryType: {},
      popupActive: false
    }
  },
  methods: {
    async load () {
      let self = this
      BeneficiaryTypeService.beneficiaryTypes({
        typeId: this.$route.params.id
      }).then(function (response) {
        self.beneficiaryType = response.data.beneficiaryTypeList[0] || {}
      })
    },
    cancel () {
      this.$router.back()
    },
    modify () {
      this.$router.push({ name: 'ModifyBeneficiaryType', params: { id: this.$route.params.id } })
    }
  },
  mounted () {
    this.load()
  },
  watch: {
    $route (to, from) {
      this.load()
    }
  }
}
</script>
